.card {
  padding: 16px 12px;
}

.cardTitle {
  display: block;
  text-decoration: none;
  color: #54adff;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.4;
  text-align: center;
  margin-bottom: 16px;

  @media screen and (min-width: 768px) {
    font-size: 16px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 20px;
  }
}

.iconDescriptionWrapper {
  display: flex;
  justify-content: flex-start;
}

.icon {
  width: 90px;
  height: 70px;
  margin-top: 12px;
  margin-right: 12px;
}

.descriptionEl {
  margin-bottom: 12px;
}

.descriptionTitle {
  font-size: 12px;
  line-height: 1.3;
  font-weight: 600;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
  }
}

.descriptionTitleDark {
  color: #fef9f9;
}

.description {
  font-size: 12px;
  line-height: 1.3;
  font-weight: 500;
  color: black;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
  }
}

.descriptionDark {
  color: #fef9f9;
}

.timeHoverDescription {
  position: relative;
  cursor: pointer;
}

.timeOverlay {
  position: absolute;
  display: flex;
  gap: 12px;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 160px;
  border: 1px solid #54adff;
  box-shadow: 3px 8px 14px rgba(136, 198, 253, 0.19);
  border-radius: 8px;
  background-color: white;
}

.timeOverlayDark {
  background-color: #2b3e51;
}

.timeDescription {
  font-size: 12px;
  line-height: 1.3;
  font-weight: 500;
  margin-bottom: 4px;
}

.timeDescriptionDark {
  color: #fef9f9;
}
